.header {
    display: flex;
    background-color: royalblue;
    align-items: center;
    justify-content: space-between;
    padding: 0 40px;

}

.wrapper{
    display: flex;
    width: 300px;
    align-items: center;
}

.logo {
    height: 60px;
}

.loginBlock a {

    text-decoration: none;
}

.loginBlock {

    width: 300px;
}

.login a{
    color: darkblue;
}

.login{
    color: darkblue;
}

.quit {
    font-size: 15px;
    color: red;
    font-family: 'Roboto', sans-serif;
    font-weight: 300;
    font-style: italic;
    float: right;
    margin-right: 10px;
    margin-top: 10px;
    background-color: #dbdada;
    border-radius: 50px;
    padding: 5px 0;
    text-align: center;
    width: 200px;
    opacity: 50%;
    transition: opacity 0.5s ease;
}

.quit:hover {
    opacity: 100%;
    font-style: normal;
    font-weight: 400;
}